.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.header-link {
  color: rgb(0, 42, 105);
  font-weight: 600;
}

.color-red {
  color: #EF476F
}

.color-yellow {
  color: #FFD166
}

.color-green {
  color: #06D6A0
}

.color-blue {
  color: #2696EA
}

.color-black-blue-text {
  color: #002A69
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}